body{
    font-family:'Times New Roman', Times, serif;
    background-color: #fff ;

}

.wrapper{
    width: 100%;
    height: 100%;
    top: 0; 
    left: 0;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
}

h1{
    text-align:center; 
    font-size:25px; 
}
p{
    padding-top: 10px;
    padding-bottom: 5px;
}
#add{
    width:100%;
    height:30px;
    font-size:15px;
}
#list{
    font-size:14px;
}
#go{
    cursor:pointer;
    text-align:center; 
    padding: 6px;
    width:50%;
    font-size:16px;
    border-radius: 6px;
    text-align: center;
    background-color:#e0feee ;
    box-shadow: 0 0.1rem 0.1rem  #68ce98;
}

.tab{
    cursor: pointer;
    padding: 10px 30px;
    margin: 0px 2px;
    background-color: #F2FBF7;
    display: inline-block;
    color: black;
    border-radius: 5px 5px 0px 0px;
    box-shadow: 0 0.1rem 0.3rem #68ce98;
}

.panels{
    background-color: transparent;
    width: 25%; 
    height: 70%;
    max-width: 80%;
    border-radius: 5px;
    overflow: hidden;
    padding: 3px;
}

.panel{
    display:none;
    animation: fadein .8s;
  }

.radio{
    display: none;
}

#login:checked ~ .panels #login-panel,
#signup:checked ~ .panels #signup-panel{
  display:block
}
#login:checked ~ .tabs #login-tab,
#signup:checked ~ .tabs #signup-tab{
  background:#fff;
  color:#000;
  border-top: 3px solid #f2f7ca;
}

